<template>
  <v-app>
    <div class="background pa-5">
      <v-progress-circular
        :size="140"
        :width="14"
        v-if="loading"
        indeterminate
        color="rgb(240, 226, 150)"
        class="middle"
      ></v-progress-circular>
      <Monaco
        class="editor"
        language="json"
        :code="json"
        @mounted="onMounted"
        >
      </Monaco>
      <v-btn color="success" @click="save">Save</v-btn>
    </div>
  </v-app>
</template>

<script>
import axios from 'axios';
import Monaco from 'monaco-editor-forvue';

export default {
  name: 'App',
  data() {
    return {
      urlGet: '',
      urlAdd: '',
      id: 0,
      json: 'null',
      res: {
        json: {},
        info: {},
        section: ''
      },
      loading: true
    }
  },
  components: {
    Monaco
  },
  methods: {
    onMounted(editor) {
      this.editor = editor;
    },
    async defaults(step) {
      let configs = await this.$store.state.json.filter(ele => ele["Hostname"] == this.id)[0]["System Config"]
      if (step == 'saved') {
        configs.Serial = configs.Serial + 1
      }
      this.json = JSON.stringify(configs, undefined, 4)
      this.editor.setValue(this.json)
    },
    save() {
      console.log("Saved!")
      this.res.json = JSON.parse(this.editor.getValue())
      this.res.json.Serial = this.res.json.Serial + 1
      let configs = this.$store.state.json.filter(ele => ele["Hostname"] == this.id)[0]
      this.res.info.Username = configs.Username
      this.res.info.Password = configs.Password
      this.res.info.Hostname = configs.Hostname
      this.res.section = 'Coins Config'

      console.log(JSON.stringify(this.res))
      axios.post(this.urlAdd, this.res)
        .catch(function (error) {
            console.error(error);
        });

      this.defaults('saved')      
    }
  },
  mounted() {
    if (window.location.href.includes('localhost')) {
      this.urlGet = "http://localhost:5000/db"
      this.urlAdd = "http://localhost:5000/rig/add"
    } else if (window.location.href.includes('192.168')) {
      this.urlGet = "http://192.168.0.127:5000/db"
      this.urlAdd = "http://192.168.0.127:5000/rig/add"
    } else {
      this.urlGet = "https://nos-server.now.sh/db"
      this.urlAdd = "https://nos-server.now.sh/rig/add"
    }
    
    this.$nextTick(() => {
      window.addEventListener('resize', () => {
        this.editor.layout()
      });
    })

    this.loading = true
    axios.post(this.urlGet)
      .then(res => {
        this.loading = false
        if (res.data == "not logged in!") this.$router.push('/')
        else {
          this.id = this.$route.params.id;
          this.defaults()
        }
    })
  }
};
</script>

<style scoped>
.editor {
  width: 100%;
  height: 75%;
  max-height: 75%;
}
.centerText{
  text-align: center;
  text-decoration: underline;
}
</style>

